<template>
  <div class="Home">
    <img class="home-bg" src="@/assets/imgs/home.jpeg" alt="">

    <wap-subtitle subtitle="高考服务"></wap-subtitle>
    <van-row gutter="20" style="padding: 10px 0;">
      <van-col span="8" class="service" @click.native="$router.push({name: 'Score'})">
        <div class="img">
          <svg class="icon-font" aria-hidden="true">
            <use xlink:href="#icon-chengji"></use>
          </svg>
        </div>
        <span>成绩查询</span>
      </van-col>
      <van-col span="8" class="service" @click.native="$router.push({name: 'Test'})">
        <div class="img">
          <svg class="icon-font" aria-hidden="true">
            <use xlink:href="#icon-ceshi"></use>
          </svg>
        </div>
        <span>录取概率测试</span>
      </van-col>
      <van-col span="8" class="service" @click.native="$router.push({name: 'Apply'})">
        <div class="img">
          <svg class="icon-font" aria-hidden="true">
            <use xlink:href="#icon-tianbao"></use>
          </svg>
        </div>
        <span>智能填报</span>
      </van-col>
    </van-row>

    <wap-subtitle subtitle="其他服务"></wap-subtitle>

    <van-row gutter="20" style="padding: 10px 0;">
      <van-col span="8" class="service">
        <div class="img">
          <svg class="icon-font" aria-hidden="true">
            <use xlink:href="#icon-zhuanjia"></use>
          </svg>
        </div>
        <span>专家解读</span>
      </van-col>
      <van-col span="8" class="service" @click.native="$router.push({name: 'Qrcode'})">
        <div class="img">
          <svg class="icon-font" aria-hidden="true">
            <use xlink:href="#icon-zixun"></use>
          </svg>
        </div>
        <span>咨询热线</span>
      </van-col>

    </van-row>

    <van-tabbar v-model="active">
      <van-tabbar-item icon="shop" :to="{name: 'Home'}">首页</van-tabbar-item>
      <van-tabbar-item icon="chat" :to="{name: 'Mine'}">我的</van-tabbar-item>
    </van-tabbar>
  </div>

</template>

<script>
export default {
  data() {
    return {
      active: 0
    }
  }
}
</script>


<style lang="scss" scoped>
.home-bg {
  width: 100%;
  margin-bottom: 20px;
}

.service {
  display: flex;
  flex-direction: column;
  text-align: center;
  .img {
    position: relative;
    bottom: -5px;
    color: green;
  }

  span {
    font-size: 14px;
    padding-top: 10px;
  }
}
.icon-font {
  width: 2em;
  height: 2em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>



